body{
  transition-delay:.4s;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.para
{
  background-color: black;
  color: white;
  text-align: center;
  font-weight: bolder;
}
.navbar
{
  box-shadow: 1px 2px 5px red;
  list-style: none;
  margin: 20px;
  padding: 10px;
  display: flex;
}
.navbar li
{
  padding-left: 10px;
  font-weight: bolder;
}
.info
{
  margin-left: 50px;
}
.center
{
  margin-left: 100px;
}
.ntbn
{
  margin: 10px;
}
.form-check-label{
  color: rgb(152, 241, 10);
  font-weight: bolder;
}